module.exports = ".content-div {\n  background: #fff;\n  border-radius: 50px 50px 0px 0px;\n  margin-top: -50px;\n  position: relative;\n  width: 100%;\n  min-height: calc(100vh - 200px);\n}\n\n.empty-div {\n  background: #0A15D5;\n  height: 200px;\n}\n\n.empty-div ion-item {\n  margin-bottom: 10px;\n}\n\n.head-title {\n  color: #fff;\n  font-size: 16px;\n  font-weight: bold;\n}\n\n.custom-icon-calendar-icon {\n  font-size: 25px;\n}\n\n.custom-icon-calendar-icon:before {\n  color: white !important;\n}\n\n.attendance-list {\n  background: none;\n  border-radius: 50px;\n  padding: 0;\n  margin: 15px 30px 10px 50px;\n  box-shadow: -7px 10px 18px 7px rgba(0, 0, 0, 0.09);\n}\n\n.attendance-list .attendance-content {\n  padding: 10px 20px 10px 20px;\n}\n\n.attendance-content {\n  top: 0;\n  width: 100%;\n}\n\n.attendance-timeline {\n  position: relative;\n  max-width: 1200px;\n  margin: 0 auto;\n  margin-top: 10px;\n}\n\n/* The actual timeline (the vertical ruler) */\n\n.attendance-timeline::after {\n  content: \"\";\n  position: absolute;\n  width: 2px;\n  background-color: #D2D2D2;\n  top: 0;\n  bottom: 0;\n  left: 50%;\n}\n\n/* Container around content */\n\n.attendance-container {\n  position: relative;\n  background-color: inherit;\n}\n\n/* The circles on the timeline */\n\n.attendance-container::after {\n  content: \"\";\n  position: absolute;\n  width: 15px;\n  height: 15px;\n  top: 20px;\n  border-radius: 50%;\n  z-index: 1;\n  margin-left: 9px;\n}\n\n.attendance-container:nth-of-type(odd)::after {\n  background-color: #0A15D5;\n}\n\n.attendance-container:nth-of-type(even)::after {\n  background-color: #FF9F55;\n}\n\n/* Place the container to the left */\n\n.left {\n  left: 0;\n}\n\n/* Place the timelime to the left */\n\n.attendance-timeline::after {\n  left: 31px;\n}\n\n/* Make sure all circles are at the same spot */\n\n.left::after,\n.right::after {\n  left: 15px;\n}\n\n.attendance-content ion-row ion-col {\n  padding: 0;\n}\n\n.attendance-content .activity-in,\n.attendance-content .activity-out {\n  font-family: \"Poppins-Regular\";\n}\n\n.attendance-content .time-in,\n.attendance-content .time-out {\n  font-family: \"Poppins-Regular\";\n  font-weight: bold;\n  white-space: nowrap;\n}\n\n.attendance-content .activity-out {\n  text-align: end;\n}\n\n.attendance-content .time-out {\n  text-align: end;\n}\n\n.attendance-hours {\n  border-radius: 30px;\n  border: solid 1px #FF9F55;\n  color: #FF9F55;\n  font-weight: 600;\n  font-size: 20px;\n  text-align: center;\n  margin: 25px 30px;\n  padding: 10px;\n}\n\n.seg-title {\n  font-size: 18px;\n  font-weight: bold;\n  margin-left: 50px;\n}\n\n.border {\n  margin-left: 50px;\n  border-top: solid 2px #FE9C45;\n  width: 80px;\n}\n\n.page-btn {\n  padding: 20px;\n}\n\n.page-btn ion-col {\n  margin: auto 0;\n}\n\n.page-btn ion-col ion-icon,\n.page-btn span {\n  color: #ff9f55;\n  font-size: 25px;\n}\n\n.page-btn ion-col ion-icon {\n  background: rgba(247, 208, 179, 0.8117647059);\n  padding: 6px;\n  border-radius: 50px;\n}\n\n.page-btn ion-col span {\n  font-size: 16px !important;\n}\n\n.page-btn .end {\n  text-align: end;\n}\n/*# sourceMappingURL=data:application/json;base64, */"